<template>
  <div class="app-container" :class="{isHasBottomNav: isHasBottomNav }">
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>

    <router-view v-if="!$route.meta.keepAlive"></router-view>
  </div>
</template>

<script>
  export default {
    name: 'layout',
    data() {
      return {
        isHasBottomNav: true
      }
    },
    mounted() {

    },
    methods: {

    },
    watch: {
      '$route': function (to, from){
        this.$indicator.close();
        if (to.name === 'business' || to.name === 'finance' || to.name === 'personInfo') {
          this.isHasBottomNav = true;
        } else {
          this.isHasBottomNav = false;
        }
      }
    },
  }
</script>

<style scoped lang="scss">
  .app-container {
    position: relative;

    &.isHasBottomNav {
      padding-bottom: 50px;
    }
  }
</style>



